import NextAuth from "next-auth"

const providerName = process.env.OPENID_PROVIDER_NAME ?? "Single Sign-On"
const openIdScope = process.env.OPENID_SCOPE ?? "openid profile email"
const openIdPrompt = process.env.OPENID_PROMPT

function assertEnv(value, message) {
  if (!value) {
    throw new Error(message)
  }
}

const {
  OPENID_WELL_KNOWN,
  OPENID_ISSUER,
  OPENID_CLIENT_ID,
  OPENID_CLIENT_SECRET,
  NEXTAUTH_SECRET,
} = process.env

assertEnv(
  OPENID_WELL_KNOWN || OPENID_ISSUER,
  "Set either OPENID_WELL_KNOWN or OPENID_ISSUER in your environment to configure the OpenID provider."
)
assertEnv(
  OPENID_CLIENT_ID,
  "Set OPENID_CLIENT_ID in your environment to configure the OpenID provider."
)
assertEnv(
  OPENID_CLIENT_SECRET,
  "Set OPENID_CLIENT_SECRET in your environment to configure the OpenID provider."
)
assertEnv(
  NEXTAUTH_SECRET,
  "Set NEXTAUTH_SECRET in your environment so Auth.js can encrypt sessions."
)

const { handlers, auth, signIn, signOut } = NextAuth({
  providers: [
    {
      id: "oidc",
      name: providerName,
      type: "oidc",
      wellKnown: OPENID_WELL_KNOWN,
      issuer: OPENID_ISSUER,
      clientId: OPENID_CLIENT_ID,
      clientSecret: OPENID_CLIENT_SECRET,
      authorization: {
        params: {
          scope: openIdScope,
          ...(openIdPrompt ? { prompt: openIdPrompt } : {}),
        },
      },
      profile(profile) {
        const fallbackName = [profile.given_name, profile.family_name]
          .filter(Boolean)
          .join(" ")

        return {
          id: profile.sub ?? profile.id ?? profile.user_id,
          name: profile.name ?? fallbackName ?? "",
          email: profile.email ?? null,
          image: profile.picture ?? null,
        }
      },
    },
  ],
  session: {
    strategy: "jwt",
  },
  pages: {
    signIn: "/auth/signin",
  },
  trustHost: true,
  secret: NEXTAUTH_SECRET,
  callbacks: {
    authorized({ request, auth }) {
      const isLoggedIn = !!auth?.user
      const pathname = request.nextUrl.pathname

      if (pathname.startsWith("/auth/signin")) {
        return true
      }

      return isLoggedIn
    },
    jwt({ token, account, profile }) {
      if (account) {
        token.accessToken = account.access_token
        token.id = profile?.sub ?? profile?.id ?? token.sub
      }

      if (!token.id && token.sub) {
        token.id = token.sub
      }

      return token
    },
    session({ session, token }) {
      if (session.user) {
        session.user.id = token.id
      }

      if (token.accessToken) {
        session.accessToken = token.accessToken
      }

      return session
    },
  },
})

export { handlers, auth, signIn, signOut }
